import React, {useContext, useEffect, useRef, useState} from 'react';
import SortIcon from '@mui/icons-material/Sort';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import DeleteIcon from '@mui/icons-material/Delete';
import WestIcon from '@mui/icons-material/West';
import EastIcon from '@mui/icons-material/East';
import InfoIcon from '@mui/icons-material/Info';
import LockIcon from '@mui/icons-material/Lock';
import LockOpenIcon from '@mui/icons-material/LockOpen';
import MenuIcon from '@mui/icons-material/Menu';
import DriveFileRenameOutlineIcon from '@mui/icons-material/DriveFileRenameOutline';
import CalculateIcon from '@mui/icons-material/Calculate';
import DataObjectIcon from '@mui/icons-material/DataObject';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemIcon from '@mui/material/ListItemIcon';
import { sortBy } from './algorithm'
import {DataContext} from './dataContext'
import { AbridgedUrlLink } from './common-components';
import { SelectDialog } from './SelectDialog'
import { TextFieldDialog } from './TextFieldDialog'
import {InsertLeftIcon} from './icon/InsertLeftIcon'
import {InsertRightIcon} from './icon/InsertRightIcon'
import {SortAscendingIcon} from './icon/SortAscendingIcon'
import { ColumnMetadataDialog } from './ColumnMetadataDialog';
import { CheckListDialog } from './CheckListDialog';

function TableView(props) {
  return (
    <div id="tableview" style={props.style}>
      <DataTable />
    </div>
  );
}

function DataTable() {
  const context = useContext(DataContext);
  const [sorting, setSorting] = useState(null);
  const [disabled, setDisabled] = useState(true);

  const features = context.filteredData;
  const refs = useRef({});
  // useEffect to update the ref on data update
  useEffect(() => {
    refs.current = {};
  }, [context.columns]);

  if (!context.filteredData) return null;

  const handleKeyDown = (e, row, col) => {
    // lock table using Ctrl+S
    if (e.key === 's' && (e.ctrlKey || e.metaKey)) {
      e.preventDefault();
      setDisabled(true);
    }
    // paste using Ctrl+V
    if (e.key === 'v' && (e.ctrlKey || e.metaKey)) {
      e.preventDefault();
      const target = refs.current?.[row]?.[col];
      if (target) {
        navigator.clipboard.readText().then((result) => {
          const pasteTable = result.replaceAll('\r', '').split('\n').map((r) => r.split('\t'));
          const baseColIdx = context.columns.findIndex((c) => c.name === col);
          const newFeatures = context.filteredData
            .map((feature, fidx) => {
            if (fidx >= row && fidx < row + pasteTable.length) {
              const pasteRow = fidx - row;
              const newProperties = pasteTable[pasteRow].reduce((acc, v, i) => {
                if (baseColIdx + i < context.columns.length) {
                  const colName = context.columns[baseColIdx+i].name;
                  refs.current[fidx][colName].value = v;
                  return {...acc, [colName]: v};
                }
                return acc;
              }, feature.properties);
              return {...feature, properties: newProperties};
            } else {
              return feature;
            }
          });
          const updatedData = context.data.map((feature) => {
            const replacement = newFeatures.find((newf) => newf.id === feature.id);
            return replacement ?? feature;
          })
          context.setData(updatedData);
        })
      }
    }
    // use arrows/enter to navigate cells
    let focusTarget = null;
    if (refs.current?.[row]?.[col]) {
      if (e.key === 'Down' || e.key === 'ArrowDown' || (e.key === 'Enter' && !e.shiftKey)) {
        focusTarget = refs.current[Math.min(row+1, features.length-1)][col];
      } else if (e.key === 'Up' || e.key === 'ArrowUp' || (e.key === 'Enter' && e.shiftKey)) {
        focusTarget = refs.current[Math.max(row-1, 0)][col];
      }
      if (focusTarget) {
        e.preventDefault();
        focusTarget.focus();
        focusTarget.select();
      }
    }
  }
  const handleSortingChange = (newSorting) => {
    if (context.sorting && newSorting &&
      context.sorting[0] === newSorting[0] &&
      context.sorting[1] === newSorting[1]) {
      return;
    }
    setSorting(newSorting);
    context.setData(sortBy(context.data, newSorting).slice());
  };
  const handleRowChange = (newRow, idx) => {
    const newFeatures = context.data.map((f) => f.id === features[idx].id ? {...f, properties: newRow} : f);
    context.setData(newFeatures);
  };

  return (
    <table id="data-table" cellSpacing={0}>
      <thead>
        <TableHeader
          columns={context.columns}
          setColumns={context.setColumns}
          sorting={sorting}
          setSorting={handleSortingChange}
          disabled={disabled}
          setDisabled={setDisabled} />
      </thead>
      <tbody>
        {features.map((feature, fidx) =>
          <TableRow
            key={feature.id}
            cellRefs={refs}
            handleKeyDown={handleKeyDown}
            columns={context.columns}
            fidx={fidx}
            feature={feature}
            rowId={feature.id}
            onChange={handleRowChange}
            active={context.active !== null && feature.id === context.active}
            setActive={context.setActive}
            disabled={disabled}
            />)}
      </tbody>
    </table>
  );
}

function ColumnContextMenu(props) {
  const context = useContext(DataContext);
  const [contextMenu, setContextMenu] = React.useState(null);
  const [renameDialogOpen, setRenameDialogOpen] = React.useState(false);
  const [retypeDialogOpen, setRetypeDialogOpen] = React.useState(false);
  const [calculateDialogOpen, setCalculateDialogOpen] = React.useState(false);
  const [insertDialog, setInsertDialog] = React.useState(null);
  const [columnMetadataOpen, setColumnMetadataOpen] = React.useState(false);
  
  const setInvisible = (fieldname) => {
    context.setColumns(context.columns.map((col) => col.name === fieldname ? {...col, visible: false} : col));
  };
  const deleteColumn = (fieldname) => {
    // delete from column list
    context.setColumns(context.columns.filter((col) => col.name !== fieldname));
    // delete from data as well
    context.setData(context.data.map((feature) => { let {[fieldname]: _, ...rest} = feature.properties; return {...feature, properties: rest}; }));
  };
  const renameColumn = (oldname, newname) => {
    if (oldname === newname) return;
    // rename in the column list
    context.setColumns(context.columns.map((col) => col.name === oldname ? {...col, name: newname} : col));
    // rename in the data as well
    context.setData(context.data.map((feature) => { let {[oldname]: _, ...rest} = feature.properties; return {...feature, properties: {...rest, [newname]: feature.properties[oldname]}}; }));
  };
  const retypeColumn = (name, newtype) => {
    // retype in the column list
    context.setColumns(context.columns.map((col) => col.name === name ? {...col, type: newtype} : col));
  };
  const calculateColumn = (name, formula) => {
    let userFunction = undefined;
    try {
      userFunction = new Function("feature", "index", formula);
      if (userFunction) {
        context.setData(context.data.map((feature, index) => {
          let {[name]: _, ...rest} = feature.properties;
          return {...feature, properties: {...rest, [name]: userFunction(feature, index)}};
        }));
      }
    }
    catch (error) {
      alert(`Error parsing formula "${formula}": ${error.message}`);
    }
  };
  const swapColumns = (i1, i2) => {
    if (i1 < 0 || i2 < 0 || i1 >= context.columns.length || i2 >= context.columns.length) return;
    context.setColumns(context.columns.map((col, i) =>
      i === i1 ? context.columns[i2] : i === i2 ? context.columns[i1] : col));
  };
  const insertColumn = (i, name) => {
    if (i <= 0) {
      context.setColumns(
        [{name, visible: true, type: "string"},
        ...context.columns]);
    } else {
      context.setColumns([...context.columns.slice(0,i),
        {name, visible: true, type: "string"},
        ...context.columns.slice(i,context.columns.length)]);
    }
  };

  const handleContextMenu = (event) => {
    setContextMenu(
      contextMenu === null
        ? {
            mouseX: event.clientX + 2,
            mouseY: event.clientY - 6,
          }
        : // repeated contextmenu when it is already open closes it with Chrome 84 on Ubuntu
          // Other native context menus might behave different.
          // With this behavior we prevent contextmenu from the backdrop to re-locale existing context menus.
          null,
    );
  };

  const handleClose = () => {
    setContextMenu(null);
  };

  return (
    <React.Fragment>
      <span onClick={handleContextMenu}>
        {props.children}
      </span>
      <Menu
        open={contextMenu !== null}
        onClose={handleClose}
        anchorReference="anchorPosition"
        anchorPosition={
          contextMenu !== null
            ? { top: contextMenu.mouseY, left: contextMenu.mouseX }
            : undefined
        }
      >
        <MenuItem
          onClick={() => { props.setSorting({col: context.columns.find((c) => c.name === props.columnName), asc: true}); handleClose() }}>
          <ListItemIcon>
            <SortAscendingIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Sort Ascending</ListItemText>
        </MenuItem>
        <MenuItem
          onClick={() => { props.setSorting({col: context.columns.find((c) => c.name === props.columnName), asc: false}); handleClose() }}>
          <ListItemIcon>
            <SortIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Sort Descending</ListItemText>
        </MenuItem>
        <MenuItem
          onClick={() => { setRenameDialogOpen(true); handleClose() }}>
          <ListItemIcon>
            <DriveFileRenameOutlineIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Rename</ListItemText>
        </MenuItem>
        <MenuItem
          onClick={() => { setRetypeDialogOpen(true); handleClose() }}>
          <ListItemIcon>
            <DataObjectIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Change Type</ListItemText>
        </MenuItem>
        <MenuItem
          onClick={() => { setCalculateDialogOpen(true); handleClose() }}>
          <ListItemIcon>
            <CalculateIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Calculate</ListItemText>
        </MenuItem>
        <MenuItem
          onClick={() => { swapColumns(props.columnIndex, props.columnIndex-1); handleClose() }}>
          <ListItemIcon>
            <WestIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Shift left</ListItemText>
        </MenuItem>
        <MenuItem
          onClick={() => { swapColumns(props.columnIndex, props.columnIndex+1); handleClose() }}>
          <ListItemIcon>
            <EastIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Shift right</ListItemText>
        </MenuItem>
        <MenuItem
          onClick={() => { setInsertDialog("left"); handleClose() }}>
          <ListItemIcon>
            <InsertLeftIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Insert left</ListItemText>
        </MenuItem>
        <MenuItem
          onClick={() => { setInsertDialog("right"); handleClose() }}>
          <ListItemIcon>
            <InsertRightIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Insert right</ListItemText>
        </MenuItem>
        <MenuItem
          onClick={() => { setInvisible(props.columnName); handleClose() }}>
          <ListItemIcon>
            <VisibilityOffIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Hide</ListItemText>
        </MenuItem>
        <MenuItem
          onClick={() => { deleteColumn(props.columnName); handleClose() }}>
          <ListItemIcon>
            <DeleteIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Delete</ListItemText>
        </MenuItem>
        <MenuItem
          onClick={() => { setColumnMetadataOpen(true); handleClose() }}>
          <ListItemIcon>
            <InfoIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Metadata</ListItemText>
        </MenuItem>
      </Menu>
      <ColumnMetadataDialog
        open={columnMetadataOpen}
        onClose={() => setColumnMetadataOpen(false)}
        column={context.columns.find((c) => props.columnName === c.name)}
        data={context.filteredData.map((row) => row.properties[props.columnName])}
        />
      <SelectDialog
        title={`Change data type of column '${props.columnName}'?`}
        label="Type"
        confirmLabel="Change"
        defaultValue={context.columns[props.columnIndex].type}
        options={["date", "number", "string"]}
        open={retypeDialogOpen}
        onConfirm={(newtype) => { retypeColumn(props.columnName, newtype); setRetypeDialogOpen(false); }}
        onCancel={() => setRetypeDialogOpen(false)}
      />
      <TextFieldDialog
        title={`Rename column '${props.columnName}'?`}
        label="Name"
        confirmLabel="Rename"
        defaultValue={props.columnName}
        open={renameDialogOpen}
        onConfirm={(newname) => { renameColumn(props.columnName, newname); setRenameDialogOpen(false); }}
        onCancel={() => { setRenameDialogOpen(false); }}
      />
      <TextFieldDialog
        title={`Calculate values for column '${props.columnName}'`}
        label="Formula"
        confirmLabel="Calculate"
        defaultValue={`return feature.properties["${props.columnName}"];`}
        open={calculateDialogOpen}
        onConfirm={(formula) => { calculateColumn(props.columnName, formula); setCalculateDialogOpen(false); }}
        onCancel={() => { setCalculateDialogOpen(false); }}
        multiline
      />
      <TextFieldDialog
        title={`Insert column ${insertDialog} of '${props.columnName}'?`}
        confirmLabel="Insert"
        defaultValue={null}
        open={insertDialog !== null}
        onConfirm={(newname) => {
          insertColumn(props.columnIndex + (insertDialog === "left" ? 0 : 1), newname);
          setInsertDialog(null); }}
        onCancel={() => setInsertDialog(null)}
      />
    </React.Fragment>
  );
}

function TableContextMenu(props) {
  const [contextMenu, setContextMenu] = useState(null);
  const [visibilityOpen, setVisibilityOpen] = useState(false);
  const handleContextMenu = (event) => {
    setContextMenu(
      contextMenu === null
        ? {
            mouseX: event.clientX + 2,
            mouseY: event.clientY - 6,
          }
        : // repeated contextmenu when it is already open closes it with Chrome 84 on Ubuntu
          // Other native context menus might behave different.
          // With this behavior we prevent contextmenu from the backdrop to re-locale existing context menus.
          null,
    );
  };
  const handleClose = () => {
    setContextMenu(null);
  };

  return (
    <React.Fragment>
      <span onClick={handleContextMenu}>
        {props.children}
      </span>
      <Menu
        open={contextMenu !== null}
        onClose={handleClose}
        anchorReference="anchorPosition"
        anchorPosition={
          contextMenu !== null
            ? { top: contextMenu.mouseY, left: contextMenu.mouseX }
            : undefined
        }
      >
        <MenuItem
          onClick={() => { props.setDisabled(!props.disabled); handleClose() }}>
          <ListItemIcon>
            {props.disabled ? <LockOpenIcon fontSize="small" /> : <LockIcon fontSize="small" />}
          </ListItemIcon>
          <ListItemText>{props.disabled ? "Unl" : "L"}ock Values</ListItemText>
        </MenuItem>
        <MenuItem
          onClick={() => { setVisibilityOpen(true); handleClose() }}>
          <ListItemIcon>
            <VisibilityIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText>Set Visible Columns</ListItemText>
        </MenuItem>
      </Menu>
      <CheckListDialog
        open={visibilityOpen}
        onCancel={() => setVisibilityOpen(false)}
        onConfirm={(draft) => { props.setColumns(draft); setVisibilityOpen(false)}}
        defaultValue={props.columns}
        title="Select Visible Columns"
        labelColumn="name"
        checkedColumn="visible"
        />
    </React.Fragment>
  );
}

function TableHeader(props) {
  return (
    <tr>
      <th className="tableCorner">
        <TableContextMenu
          disabled={props.disabled}
          setDisabled={props.setDisabled}
          columns={props.columns}
          setColumns={props.setColumns}
          >
            <MenuIcon />
        </TableContextMenu>
      </th>
      {Array.from(props.columns).map((column, idx) => {return {column, idx}}).filter((info) => info.column.visible).map((info) =>
        <th key={info.column.name} >
          <span onClick={() => {
            props.setSorting({col: info.column, asc: (props.sorting && props.sorting.col.name === info.column.name) ? !props.sorting.asc : true});
          }}>
            {info.column.name}
          </span>
          <ColumnContextMenu columnName={info.column.name} columnIndex={info.idx} setSorting={props.setSorting}>
            <MoreVertIcon className="inlineIcon" />
          </ColumnContextMenu>
        </th>)}
    </tr>
  );
}

function TableRow(props) {
  const handleCellChange = (value, column) => {
    const newFeatureProperties = {...props.feature.properties, [column.name]: value};
    props.onChange(newFeatureProperties, props.fidx);
  };
  return (
    <tr
      onContextMenu={() => console.log(props.feature)}
      onClick={() => props.setActive(props.feature.id)}
      className={props.active ? "active" : ""}
      >
      <th>{1+props.fidx}</th>
      {Array.from(props.columns).filter((column) => column.visible).map((column) =>
        <TableCell
          key={`${props.rowId}:${column.name}`}
          cellRefs={props.cellRefs}
          handleKeyDown={props.handleKeyDown}
          column={column}
          fidx={props.fidx}
          value={props.feature.properties[column.name]}
          onChange={handleCellChange}
          disabled={props.disabled}
          />)}
    </tr>
  );
}

function TableCell(props) {
  const handleBlur = (e) => {
    props.onChange(e.target.value, props.column);
  };

  if (props.disabled) {
    return (
      <td>
        <CellValue value={props.value} column={props.column} />
      </td>
    );
  }
  return (
    <td>
      <input
        ref={el => {
          if (!props.cellRefs.current.hasOwnProperty(props.fidx)) {
            props.cellRefs.current[props.fidx] = {};
          }
          props.cellRefs.current[props.fidx][props.column.name] = el
        }}
        onKeyDown={(e) => props.handleKeyDown(e, props.fidx, props.column.name)}
        type="text"
        defaultValue={props.value ?? ""}
        size={props.value?.length ?? 17 + 3}
        onBlur={handleBlur}
        />
    </td>
  );
}

function CellValue(props) {
  // transform null, undefined, empty string to undefined
  if (props.value === null || props.value === undefined || props.value === "") {
    return undefined;
  }
  // transform URLs to links
  if (typeof props.value === "string" && props.value.startsWith("http")) {
    return <AbridgedUrlLink target="_blank" href={props.value} length={21} />;
  }
  switch (props.column.type) {
    case "number":
      try {
        return Number(props.value);
      }
      catch (e) {
        alert(`Invalid number -- could not parse "${props.value}" to number:\n ${e.message}`);
      }
    case "date":
      try {
        let date = null;
        if (typeof props.value === "number") {
          date = new Date(props.value);
        } else if (typeof props.value === "string") {
          // if it contains only digits, convert to a number; otherweise, parse as date string
          const isnum = props.value.match(/^[0-9]+$/) != null;
          date = new Date(isnum ? Number(props.value) : Date.parse(props.value));
        }
        return date.toISOString();
      }
      catch (e) {
        alert(`Invalid date -- could not parse "${props.value}" to date:\n ${e.message}`);
      }
    default:
      return typeof props.value === "string" || typeof props.value === "number"
      ? props.value
      : JSON.stringify(props.value);
  }
}

export default TableView;